<i18n>
{
  "en": {
    "修改绑定邮箱": "Change Email Address",
    "已绑定邮箱": "Current email address",
    "还未绑定邮箱": "Have not bound email yet",
    "绑定并激活邮箱后，您将可以使用邮箱登录网站及接收通知": "You can sign in the website or receive notifications via email after binding and activation",
    "更换邮箱": "New email address",

    "确 定": "Submit",

    "输入新邮箱地址": "Enter new email address",
    "确认新邮箱地址": "Confirm new email address",

    "请输入新邮箱地址": "Please enter a new email address",
    "请确认新邮箱地址": "Please confirm the new email address",
    "两次输入邮箱不一致": "The two emails are inconsistent",
    "请输入正确的邮箱地址": "Please enter a valid email address",
    "新邮箱不能与原邮箱相同": "New and original email address cannot be same",
    "系统已向该邮箱发送了一封激活邮件，点击激活链接激活后将自动绑定到新邮箱。如果没有收到，请检查您的垃圾箱": "The system has sent an activation email to the address. Click the activation link and your account will automatically bind the new email. Please check your trash if not receive."
  }
}
</i18n>

<template>
  <div class="container">
    <h2
      v-t="'修改绑定邮箱'"
      class="mt0" />
    <el-form
      ref="form"
      label-position="top">
      <el-form-item :label="$t('已绑定邮箱')">
        <el-input
          :value="$store.getters.userInfo.email || $t('还未绑定邮箱')"
          disabled/>
        <el-card class="tips-card">{{ $t('绑定并激活邮箱后，您将可以使用邮箱登录网站及接收通知') }}</el-card>
      </el-form-item>
      <el-form-item :label="$t('输入新邮箱地址')">
        <el-input v-model="email"/>
      </el-form-item>
      <el-form-item :label="$t('确认新邮箱地址')">
        <el-input v-model="confirmEmail"/>
      </el-form-item>
      <el-form-item>
        <el-button
          :loading="loading"
          type="primary"
          @click="onSubmit">{{ $t('确 定') }}</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { validateEmail } from '@/utils/validate'
import { sendActiveMail } from '@/api/user'
export default {
  data () {
    return {
      email: '',
      confirmEmail: '',
      loading: false
    }
  },
  methods: {
    onSubmit () {
      const { email, confirmEmail } = this
      if (!validateEmail(email)) {
        return this.$message.warning(this.$t('请输入正确的邮箱地址'))
      }
      if (email === this.$store.getters.userInfo.email) {
        return this.$message.warning(this.$t('新邮箱不能与原邮箱相同'))
      }
      if (email !== confirmEmail) {
        return this.$message.warning(this.$t('两次输入邮箱不一致'))
      }

      this.loading = true

      sendActiveMail(email).then(() => {
        this.loading = false
        // this.email = ''
        // this.confirmEmail = ''
        this.$message({
          type: 'success',
          duration: 0,
          showClose: true,
          message: this.$t('系统已向该邮箱发送了一封激活邮件，点击激活链接激活后将自动绑定到新邮箱。如果没有收到，请检查您的垃圾箱')
        })
      }).catch(() => {
        this.loading = false
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.container {
  position: relative;
  padding: 24px 32px;
  .el-form {
    width: 600px;
    .alert {
      margin-top: 12px;
      line-height: 20px;
      color: rgba(0, 0, 0, 0.85);
      .el-button {
        padding-top: 0;
        padding-bottom: 0;
      }
    }
  }
}
.tips-card {
  position: absolute;
  top: 0px;
  left: 660px;
  width: 260px;
  font-size: 14px;
  line-height: 1.8em;
  color: rgba($color: #000000, $alpha: 0.65);
}
</style>



